* {
    box-sizing: border-box; }




$color-yellow: #FFAE00;
$color-grey: #555A60;

.container {
    position: relative;
    max-width: 1140px;
    margin: 0 auto; }

.main-section {
    position: relative;
    min-height: 2250px;
    background-color: #F1F1F1; }

.contacts {

    &__list {
        display: flex;
        flex-direction: row;
        list-style: none; }
    &__list-item {
        margin-right: 60px;
        font-size: 20px;
        &:last-child {
            margin-right: 0;
            border: 1px solid $color-yellow;
            width: 158px;
            text-align: center; }
        a {
            text-decoration: none;
            color: black; } } }

.video {
    height: 900px; }

.information {
    padding: 125px;
